.app-root {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.brand {
  display: flex;
  align-items: center;
  gap: 12px;
}

.brand img { width: 40px; height: 40px; }
.brand video { width: 40px; height: 40px; border-radius: 8px; object-fit: cover; }

.topbar {
  position: sticky;
  top: 0;
  z-index: 10;
  backdrop-filter: saturate(140%) blur(8px);
  background: color-mix(in srgb, var(--bg) 80%, transparent);
  border-bottom: 1px solid var(--border);
  padding: 0px 55px 0px 55px;
}

.topbar-inner {
  display: flex;
  margin: 0 auto;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
  padding: 10px 0px;
}

.title { font-size: 20px; font-weight: 700; }

.nav {
  display: none;
  gap: 10px;
  align-items: center;
}

/* Pill Nav container */
.pill-nav {
  padding: 6px;
  border-radius: 999px;
  display: flex;
  align-items: center;
  gap: 6px;
 }

.pill-nav a {
  color: var(--muted);
  text-decoration: none;
  font-weight: 800;
  font-size: 15px;
  padding: 10px 14px;
  border-radius: 999px;
  transition: color .15s ease, transform .15s ease, background-color .15s ease, box-shadow .15s ease;
}

.pill-nav a:hover { color: var(--text); background: color-mix(in srgb, var(--bg) 70%, transparent); transform: translateY(-1px); box-shadow: 0 6px 14px var(--shadow); }
.pill-nav a:active { transform: translateY(0); }

@media (min-width: 720px) {
  .nav { display: flex; }
}

