/* Dropdown wrapper */
.dropdown {
    position: relative;
}

/* Button that triggers dropdown */
.dropdown-button {
    background: linear-gradient( 180deg, color-mix(in srgb, var(--surface) 75%, transparent), color-mix(in srgb, var(--bg) 88%, transparent) );
    color: var(--text);
    border: 1px solid var(--border);
    border-radius: 999px;
    padding: 10px 14px;
    font-weight: 800;
    font-size: 14px;
    box-shadow: inset 0 8px 20px color-mix(in srgb, var(--bg) 80%, transparent);
    cursor: pointer;
    transition: all 0.2s ease;
}

    .dropdown-button:hover {
        transform: translateY(-1px);
        box-shadow: 0 8px 18px var(--shadow), inset 0 8px 20px color-mix(in srgb, var(--bg) 80%, transparent);
    }

/* Dropdown menu container */
.dropdown-menu {
    position: absolute;
    top: calc(100% + 8px);
    right: 0;
    min-width: 240px;
    max-width: 260px;
    max-height: 320px;
    overflow: hidden;
    background: rgba(255, 255, 255, 0.95); /* slight visibility for pointer events */
    border: none;
    border-radius: 20px;
    padding: 0;
    display: block;
    gap: 0;
    box-shadow: 0 12px 20px -6px var(--shadow), 0 24px 40px -12px var(--shadow);
    z-index: 20;
    contain: none; /* prevent pointer issues */
}

    /* ScrollStack scroller inside dropdown */
    .dropdown-menu .scroll-stack-scroller {
        position: relative;
        width: 100%;
        max-height: 320px;
        overflow-y: auto;
        overflow-x: hidden;
        overscroll-behavior: contain;
        -webkit-overflow-scrolling: touch;
        scroll-behavior: smooth;
        -webkit-transform: translateZ(0);
        transform: translateZ(0);
        will-change: scroll-position;
        pointer-events: auto;
    }

    /* ScrollStack inner content */
    .dropdown-menu .scroll-stack-inner {
        padding: 8px; /* compact for dropdown */
        min-height: auto;
    }

    /* Individual card wrapper */
    .dropdown-menu .scroll-stack-card-wrapper {
        position: relative;
    }

    /* Each theme card */
    .dropdown-menu .scroll-stack-card {
        transform-origin: top center;
        will-change: transform, filter;
        backface-visibility: hidden;
        transform-style: preserve-3d;
        box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
        width: 100%;
        height: auto;
        margin: 8px 0;
        padding: 12px 16px;
        border-radius: 16px;
        box-sizing: border-box;
        -webkit-transform: translateZ(0);
        transform: translateZ(0);
        position: relative;
        cursor: pointer;
        transition: transform 0.2s ease, box-shadow 0.2s ease;
    }

        .dropdown-menu .scroll-stack-card:hover {
            transform: translateY(-2px);
            box-shadow: 0 6px 18px rgba(0, 0, 0, 0.15);
        }

    /* Optional small end spacer for ScrollStack */
    .dropdown-menu .scroll-stack-end {
        width: 100%;
        height: 8px;
    }

/* Dropdown items (inside ScrollStack) */
.dropdown-item {
    display: flex;
    align-items: center;
    gap: 10px;
    background: transparent;
    color: var(--text);
    border: none;
    border-radius: 18px;
    padding: 10px 12px;
    text-align: left;
    font-weight: 900;
    cursor: pointer;
    transition: transform 0.15s ease;
}

    .dropdown-item:hover {
        transform: translateY(-1px);
    }

/* Swatch (small color preview) */
.swatch {
    width: 18px;
    height: 18px;
    border-radius: 6px;
    border: 1px solid var(--border);
    flex-shrink: 0;
}

.grid {
  display: grid;
  gap: 20px;
}

.footer { color: var(--muted); text-align: center; padding: 24px; }

.pill {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  border: 1px solid var(--border);
  border-radius: 999px;
  padding: 6px 10px;
}

.file-drop {
  display: grid;
  place-items: center;
  text-align: center;
  border: 2px dashed var(--border);
  border-radius: 14px;
  padding: 28px;
  color: var(--muted);
}

.file-name { color: var(--text); font-weight: 600; }

.actions { display: flex; gap: 12px; flex-wrap: wrap; }

/* Hero */
.hero {
  padding: 56px 0;
}

.hero-grid {
  display: grid;
  grid-template-columns: 1.2fr 1fr;
  gap: 36px;
  align-items: center;
}

@media (max-width: 960px) {
  .hero-grid { grid-template-columns: 1fr; }
}

.headline {
  font-size: clamp(44px, 8vw, 112px);
  line-height: 0.9;
  letter-spacing: -2px;
  font-weight: 900;
  margin: 0 0 18px 0;
}
.headline .accent {
  background: linear-gradient(90deg, var(--primary), var(--accent));
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}
.lede { color: var(--muted); font-size: clamp(16px, 2.2vw, 30px); max-width: 48ch; }

.cta { display: flex; gap: 12px; margin-top: 24px; flex-wrap: wrap; }
.btn-primary { background: var(--primary); color: #0a0a0a; font-size: 16px; }
.btn-secondary { background: transparent; color: var(--text); border: 1px solid var(--border); }

.mosaic {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 12px;
}
.mosaic-item {
  border-radius: 22px;
  overflow: hidden;
  background: #0003;
  border: 1px solid var(--border);
  aspect-ratio: 4/3;
}
.mosaic video, .mosaic img { width: 100%; height: 100%; object-fit: cover; display: block; }
